.blog-page .articles .block-items .single-page{
    .pagiantion-item1{
        width: 100%;
        border:1px solid #ddd;
        border-radius: 10px;
        display: flex;
        align-items: center;
        margin:30px 0;
        &:hover{
            .img{
                transform: scale(1.05);
                filter: brightness(0.75);
            }
            a{
                background-color: #427cff;
                color:#fff;
            }
        }
    }
    .img-cont{
        width: 35%;
        border-radius: 10px;
        overflow: hidden;
    }
    .img{
        width: 100%;
        border-radius: 10px;
        transition: 0.35s ease-in-out;
    }
    .info{
        flex: 1;
        padding:15px 55px;
    }
    .inline{
        display: flex;
        align-items: center;
        span{
            font-size: 15px;
            padding:6px 20px;
            background-color: #333;
            color:#fff;
            border-radius: 20px;
            margin-right: 20px;
        }
        small{
            font-size: 16px;
            display: flex;
            align-items: center;
            svg{
                font-size: 17px;
                margin-right: 5px;
            }
        }
    }
    h6{
        font-size: 18px;
        margin:15px 0;
    }
    p{
        font-size: 17px;
    }
    .inline2{
        display: flex;
        align-items: center;
        margin: 15px 0 25px;
        p{
            font-size: 16px;
            margin:0 7.5px
        }
        span{
            color:#555;
            margin-bottom: -3px;
        }
    }
    .profile{
        width: 50px;
    }
    .pagiantion-item1 a{
        font-size: 17px;
        display: flex;
        align-items: center;
        width: fit-content;
        color:#427cff;
        border: 1px solid #427cff;
        padding:7px 17.5px;
        border-radius: 20px;
        svg{
            margin-left: 7.5px;
        }
    }
}

@media(max-width:1401px){
    .blog-page .articles .block-items .single-page{
        .pagiantion-item1{
            margin:25px 0;
        }
        .img-cont{
            width: 35%;
        }
        .info{
            padding:10px 45px;
        }
        .inline{
            span{
                font-size: 14px;
                padding:4px 15px;
            }
            small{
                font-size: 14px;
                svg{
                    font-size: 15px;
                    margin-right: 5px;
                }
            }
        }
        h6{
            font-size: 15px;
            margin:12.5px 0;
        }
        p{
            font-size: 15px;
        }
        .inline2{
            margin: 15px 0 20px;
            p{
                font-size: 14px;
                margin:0 7.5px
            }
        }
        .profile{
            width: 40px;
        }
        .pagiantion-item1 a{
            font-size: 14px;
            padding:6px 15px;
            border-radius: 17px;
            svg{
                margin-left: 7.5px;
            }
        }
    }
}


@media(max-width:1000px){
    .blog-page .articles .block-items .single-page{
        .pagiantion-item1{
            display: block;
            width: 600px;
            max-width: 100%;
            margin:20px auto;
        }
        .img-cont{
            width: 100%;
    
        }
        .info{
            padding: 25px;
        }
        .inline2{
            flex-wrap: wrap;
            p{
                margin:7.5px 6.5px;
            }
            span{
                margin:7.5px 0 4px;
            }
        }
        .profile{
            width: 45px;
            margin: 7.5px 0;
        }
        .p{
            margin-bottom: 25px;
        }
    }
       
}

.blog-page .articles  .paginationBttns{
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 45px;
    li{
        margin:0 10px;
    }
    svg{
        font-size: 18px;
        margin-bottom: -4px;
    }
    a{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        padding:5px 0 10px;
        border:1px solid #ccc;
        border-radius: 5px;
        cursor: pointer;
        color:#333;
        margin:0;
        line-height: 1;

        font-size: 25px;
    }
    .paginationActive a{
        background-color: #427cff;
        color:white;
        border:1px solid #427cff;
    }
}

@media(max-width:1401px){
    .blog-page .articles  .paginationBttns{
        margin-top: 40px;
        li{
            margin:0 10px;
        }
        svg{
            font-size: 16px;
            margin-bottom: -4px;
        }
        a{
            width: 40px;
            height: 40px;
            padding:5px 0 10px;    
            font-size: 22px;
        }
    }
}

@media(max-width:500px){
    .blog-page .articles  .paginationBttns{
        margin-top: 35px;
        li{
            margin:0 5px;
        }
        svg{
            font-size: 16px;
            margin-bottom: -4px;
        }
        a{
            width: 35px;
            height: 35px;
            padding:5px 0 10px;    
            font-size: 19px;
        }
    }
}