.blog-page .tab-container{
    margin:2.5% 0 3.5%;
    padding:0 6%;
    .row1{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        p{
            font-size: 16.5px;
        }
    }
    .search-bar{
        display: flex;
        align-items: center;
        width: fit-content;
        border:1px solid #ccc;
        padding:0 10px 0 15px;
        border-radius: 20px;
        margin-right: 10px;
        button{
            font-size: 22.5px;
            color:#999;
            border:0;
            background: none;
            cursor: pointer;
            &:hover{
                color:#427cff;
            }
            svg{
                margin-bottom: -3px;
            }
        }
        input{
            height: 40px;
            width: 300px;
            border-radius: 20px;
            padding:0 7.5px;
            font-size: 15px;
            border:0;
            &:focus{
                outline: none;
            }
        }
    }
    .react-tabs__tab-list{
        display: flex;
        padding:15px 0;
        border-bottom: 1px solid #ccc;
        margin:20px 0 30px;
    }
    .react-tabs__tab{
        margin-right: 30px;
        font-size: 18px;
        cursor: pointer;
    }
    .react-tabs__tab--selected{
        color:#427cff;
    }
}

.blog-page .tab-container .blog-tab-item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.blog-page .blog-tab-item .item1{
    position: relative;
    width: 47.5%;
    border-radius: 10px;
    overflow: hidden;
    transition: 0.35s ease-in-out;
    &:hover{
        .img{
            transform: scale(1.05);
            transition: 0.35s ease-in-out;
            filter: brightness(0.6);
        }
    }
    .img{
        width: 100%;
        transition: 0.35s ease-in-out;
    }
    .info{
        position: absolute;
        left:0;
        right: 0;
        top:0;
        bottom:0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding:40px 50px;
        color:#fff;
    }
    .inline{
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        span{
            font-size: 15px;
            padding: 5px 20px;
            border: 1px solid #fff;
            border-radius: 15px;
            margin-right: 20px;
        }
        small{
            display: flex;
            align-items: center;
            font-size: 15px;
            svg{
                font-size: 17.5px;
                margin-right: 5px;
            }
        }
    }
    h6{
        font-size: 20px;
        margin: 7.5px 0;
    }
    .buttons{
        display: flex;
        margin-top: 15px;
        button{
            color:#fff;
            font-size: 30px;
            border:0;
            cursor: pointer;
            background: none;
            &:first-child{
                margin-right: 15px;
            }
        }
    }
    .second{
        display: flex;
        align-items: center;
        p{
            font-size: 17px;
        }
        small{
            font-size: 15px;
        }
    }
    .profile{
        content: url('../../../../assets/Blog/profile1.png');
        width: 50px;
        margin-right: 20px;
    }
}

.blog-page .blog-tab-item .items2{
    width: 47.5%;
    // border:1px solid red;
    .trending{
        display: flex;
        align-items: center;
        font-size: 18px;
        svg{
            color:#427cff;
            font-size: 35px;
            margin-right: 10px;
        }
    }
    .item2{
        width: 100%;
        border:1px solid #ccc;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        overflow: hidden;
        margin:20px 0;
        &:hover{
            box-shadow:#427cff 8px 8px;
        }
    }
    .img{
        width: 27.5%;
        border-radius: 10px;
        margin-right: 20px;
    }
    .block{
        flex:1;
        padding:15px 25px;
    }
    .inline1{
        display: flex;
        align-items: center;
        justify-content: space-between;
        span{
            padding:6.5px 17.5px 6px;
            font-size: 13.5px;
            background-color: #333;
            color:#fff;
            border-radius: 15px;
        }
        small{
            font-size: 15px;
            display: flex;
            align-items: center;
            svg{
                margin-right: 5px;
            }
        }
    }
    h6{
        font-size: 18px;
        font-weight: normal;
        margin: 15px 0;
    }
    .inline2{
        display: flex;
        align-items: center;
        p{
            font-size: 15.5px;
            margin:0 7.5px;
        }
        span{
            font-size: 16px;
            color:#444;
            margin-bottom: -3px;
        }
    }
    .profile{
        width: 47.5px;
        margin-right: 5px;
    }
}

@media(max-width:1401px){
    .blog-page .tab-container{
        .row1{
            p{
                font-size: 15px;
            }
        }
        .search-bar{
            border-radius: 17.5px;
            button{
                font-size: 20px;
                svg{
                    margin-bottom: -3px;
                }
            }
            input{
                height: 35px;
                width: 250px;
                border-radius: 17.5px;
                font-size: 14px;
            }
        }
        .react-tabs__tab-list{
            padding:10px 0;
            margin:25px 0;
        }
        .react-tabs__tab{
            margin-right: 30px;
            font-size: 15px;
        }
    }
    .blog-page .blog-tab-item .item1{
        .info{
            padding:25px 35px;
        }
        .inline{
            margin-bottom: 10px;
            span{
                font-size: 14px;
                padding: 5px 18px;
            }
            small{
                font-size: 14px;
                svg{
                    font-size: 15px;
                }
            }
        }
        h6{
            font-size: 16.5px;
            margin: 5px 0;
        }
        .buttons{
            margin-top: 15px;
            button{
                font-size: 27.5px;
                &:first-child{
                    margin-right: 10px;
                }
            }
        }
        .second{
            p{
                font-size: 15px;
            }
            small{
                font-size: 13px;
            }
        }
        .profile{
            width: 45px;
            margin-right: 15px;
        }
    }
    
    .blog-page .blog-tab-item .items2{
        .trending{
            font-size: 16px;
            svg{
                font-size: 30px;
                margin-right: 10px;
            }
        }
        .item2{
            margin:15px 0;
            &:hover{
                box-shadow:#427cff 7px 7px;
            }
        }
        .img{
            width: 29.5%;
            margin-right: 15px;
        }
        .block{
            padding:10px 20px;
        }
        .inline1{
            span{
                padding:5.5px 15px 5px;
                font-size: 13px;
            }
            small{
                font-size: 14px;
            }
        }
        h6{
            font-size: 15px;
            margin: 15px 0;
        }
        .inline2{
            p{
                font-size: 14px;
                margin:0 5px;
            }
            span{
                font-size: 15px;
                margin-bottom: -3px;
            }
        }
        .profile{
            width: 40px;
            margin-right: 2.5px;
        }
    }
}

@media(max-width:1030px){
    .blog-page .blog-tab-item .item1{
        width: 85%;
        margin:25px auto;
    }
    .blog-page .blog-tab-item .items2{
        width: 85%;
        margin:25px auto;
    }
}

@media(max-width:800px){
    .blog-page .tab-container{
        margin:5% 0;
        padding:0 7.5%;
        .row1{
            p{
                font-size: 15px;
                margin-right: 25px;
                margin:7.5px 0;
            }
        }
        .search-bar{
            margin:7.5px 0;
            width: 275px;
            max-width: 100%;
            input{
                width: unset;
                flex: 1;
            }
        }
        .react-tabs__tab-list{
            padding:10px 0;
            margin:25px 0;
            flex-wrap: wrap;
            justify-content: center;
        }
        .react-tabs__tab{
           margin:7.5px 15px;
            font-size: 16px;
        }
    }
    .blog-page .blog-tab-item .item1{
        width: 130%;
        margin:20px -10%;
        border-radius: 0;

        .info{
            padding:15px 25px;
        }
        .inline{
            span{
                font-size: 13px;
                padding: 4px 17px;
            }
        }
        h6{
            font-size: 15px;
            margin: 5px 0;
            display: inline;
        }
        .buttons{
            margin-top: 10px;
            button{
                font-size: 25px;
            }
        }
        .second{
            p{
                font-size: 14px;
            }
            small{
                font-size: 12px;
            }
        }
        .profile{
            width: 40px;
            margin-right: 15px;
        }
    }
    
    .blog-page .blog-tab-item .items2{
        width: 100%;
        .item2{
            margin:15px 0;
            padding:0px;
            flex-wrap: wrap;
            &:hover{
                box-shadow:#427cff 6px 6px;
            }
        }
        .img{
            width: 100%;
            margin:0 auto 15px;
        }
        .block{
            padding:20px;
            flex: unset;
            width: 100%;
        }
        .inline1{
            span{
                padding:5.5px 15px 5px;
                font-size: 14px;
            }
            small{
                font-size: 14.5px;
            }
        }
        h6{
            font-size: 17px;
            margin: 20px 0;
        }
        .inline2{
            flex-wrap: wrap;
            p{
                font-size: 14px;
                margin:2px 5px;
            }
            span{
                font-size: 15px;
                margin-bottom: -3px;
            }
        }
        .profile{
            width: 42.5px;
            margin:2.5px;
        }
    }
}