.blog-page{
    padding-top: 90px;
    overflow: hidden;
    .nav{
        background-color: #fff;
        border-bottom: 1px solid #ccc;

        .nav-logo{
            .logo-container{
                content: url('../../../../assets/Navbar/blue-logo.png') !important;
            }
            h6{
                color:#111 !important;
            }
        }
        .nav-links{
            .inner-nav-link{
                color:#000 !important;
                svg{
                    color:#444;
                }
            }
        }
        .nav-auth{
            .nav-login a{
                color:#000 !important;
            }
        }
        .blog-link .inner-nav-link{
            color: #427CFF !important;
        }
    }
    .nav__sticky{
        border:0;
    }
    .mobile-toolbar{
        background-color: white;
        border-bottom: 1px solid #ccc;
        transition: 0.2s ease;
        .mobile-logo-row{
            h6{
                color:#333 !important;
            }
        }
        button{
            color:#222 !important;
        }
    }
    .toolbar__sticky{
        border: 0;
    }
}
