.blog-page .articles{
    margin:2.5% 0;
    padding:0 6%;
    .list-container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        h5{
            font-size: 25px;
        }
    }
    .react-tabs__tab-list{
        display: flex;
        border:1px solid #f3f3f3;
        padding:6px;
        background-color: #f3f3f3;
        border-radius: 3px;
    }
    .react-tabs__tab{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        font-size: 32px;
        border:1px solid #eee;
        background-color: #fff;
        cursor: pointer;
        margin:0 5px;
        border-radius: 3px;
    }
    .react-tabs__tab--selected{
        color:#427cff;
    }
}

.blog-page .articles .inline-items .single-page{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .pagiantion-item1{
        width: 30%;
        border:1px solid #ddd;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        margin:20px 0;
        &:hover{
            .img{
                transform: scale(1.05);
                filter: brightness(0.75);
            }
            a{
                background-color: #427cff;
                color:#fff;
            }
        }
    }
    .img-cont{
        overflow: hidden;
        width: 100%;
        border-radius: 10px;
    }
    .img{
        width: 100%; 
        transition: 0.35s ease-in-out;
    }
    .info{
        flex: 1;
        padding:25px 35px;
        position: relative;
    }
    .inline{
        display: flex;
        align-items: center;
        justify-content: space-between;
        span{
            font-size: 15px;
            padding:6px 20px;
            background-color: #333;
            color:#fff;
            border-radius: 20px;
        }
        small{
            font-size: 16px;
            display: flex;
            align-items: center;
            svg{
                font-size: 17px;
                margin-right: 5px;
            }
        }
    }
    h6{
        font-size: 20px;
        margin:20px 0;
    }
    .p{
        font-size: 18px;
        margin-bottom: 150px;
    }
    .profile-cont{
        position: absolute;
        left:35px;
        right: 35px;
        bottom:25px;

    }
    .inline2{
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        p{
            font-size: 16px;
            margin:0 7.5px
        }
        span{
            color:#555;
            margin-bottom: -3px;
        }
    }
    .profile{
        width: 50px;
    }
    .pagiantion-item1 a{
        font-size: 17px;
        display: flex;
        align-items: center;
        width: fit-content;
        color:#427cff;
        border: 1px solid #427cff;
        padding:7px 17.5px;
        border-radius: 20px;
        margin-left: auto;
        transition: 0.25s ease-in-out;
        svg{
            margin-left: 7.5px;
        }
    }
}

@media(max-width:1575px){
    .blog-page .articles .inline-items .single-page{
        .pagiantion-item1{
            width: 32%;
            .info{
                padding:20px;
            }
        }
    }
}

@media(max-width:1401px){
    .blog-page .articles{
        margin:2.5% 0;
        .list-container{
            margin-bottom: 20px;
            h5{
                font-size: 22px;
            }
        }
        .react-tabs__tab-list{
            padding:6.5px 3px;
        }
        .react-tabs__tab{
            width: 42px;
            height: 42px;
            font-size: 30px;
        }
    }
    
    .blog-page .articles .inline-items .single-page{
        .pagiantion-item1{
            width: 32%;
            margin:15px 0;
        }
        .info{
            padding:20px;
        }
        .inline{
            span{
                font-size: 14px;
                padding:5.5px 18px;
                border-radius: 20px;
            }
            small{
                font-size: 14px;
                svg{
                    font-size: 15px;
                }
            }
        }
        h6{
            font-size: 16px;
            margin:17.5px 0;
        }
        .p{
            font-size: 15px;
            margin-bottom: 125px;
            line-height: 1.5;
        }
        .profile-cont{
            left:20px;
            right: 20px;
            bottom:20px;
    
        }
        .inline2{
            margin-bottom: 12.5px;
            p{
                font-size: 14px;
                margin:0 6.5px
            }
            span{
                margin-bottom: -3px;
            }
        }
        .profile{
            width: 45px;
        }
        .pagiantion-item1 a{
            font-size: 14.5px;
        }
    }
    
}

@media(max-width:1030px){
    .blog-page .articles .inline-items .single-page{
        .pagiantion-item1{
            width: 47.5%;
        }
    }
}
@media(max-width:630px){
    .blog-page .articles {
        .react-tabs__tab-list{
            display: none;
        }
    }
    .blog-page .articles .inline-items .single-page{
        .pagiantion-item1{
            width: 350px;
            max-width: 100%;
            margin:10px auto;
        }
        .profile-cont{
            left:unset;
            right: unset;
            bottom:unset;
            position: relative;
    
        }
        .inline2{
            flex-wrap: wrap;
            p{
                margin:7.5px 6.5px;
            }
            span{
                margin:7.5px 0 4px;
            }
        }
        .profile{
            width: 45px;
            margin: 7.5px 0;
        }
        .p{
            margin-bottom: 25px;
        }
    }
}
